export interface IMediaInfo {
  uid: string; //yes
  placeOfUpload: string;
  name: string;
  fileName: string; //yes
  category: string;
  type: string;
  sectionName: string; //smDocSectionType
  fileType: string; //fileType
  download: string;
  url: string | Promise<string>; //"url"
}

function PageApplicationMedia(props: RouteProps) {
  const params = useParams();
  /*
    -------------------------------------------------------
      States
    -------------------------------------------------------
  */

  const [isImageClicked, setIsImageClicked] = useState(false);
  const [searchObj, setSearchObj] = useState({
    documentCategory: "",
    documentType: "",
    section: "",
    stage: "",
    fileType: "",
  });
  const [dataTable, setDataTable] = useState({
    limit: 10,
    loading: false,
    totalRecords: 0,
    start: 0,
    search: "",
    runCustomQuery: false,
    records: [],
  });
  const [mediaGalleryForLogin, setMediaGalleryForLogin] = useState([]);
  const [mediaGalleryForPd, setMediaGalleryForPd] = useState([]);
  const [createdByTemplate, setCreatedByTemplate] = useState<
    {
      name?: string | undefined;
    }[]
  >([]);

  const dataTableRef = useRef(null);

  /*
    -------------------------------------------------------
      Hooks: useEffect
    -------------------------------------------------------
  */
  useEffect(() => {
    // Call fetchData when the component mounts
    fetchData();
  }, []);
  useEffect(() => {
    // @ts-ignore: Will fix later
    dataTableRef?.current?.search();
  }, [dataTable?.search]);

  /*
    -------------------------------------------------------
      Handlers: Event handler for onChange, onBlur etc
    -------------------------------------------------------
  */

  const handleAutoCompleteFilterChange = (event: any) => {
    const { name, value } = event.target;
    if (typeof value === "object") {
      setSearchObj((prevFilterObj) => ({
        ...prevFilterObj,
        [name]: { name: value?.name, id: value.userId },
      }));
    } else
      setSearchObj((prevFilterObj) => ({
        ...prevFilterObj,
        [name]: value,
      }));
  };

  const handleFilterChange = (event: ChangeEvent<HTMLInputElement> | CalendarChangeParams) => {
    const { name, value } = event.target;

    setSearchObj((prevFilterObj) => ({
      ...prevFilterObj,
      [name]: value,
    }));
  };
  const handleImageClick = () => {
    setIsImageClicked(true);
  };
  /* 
    -------------------------------------------------------
      Helper Functions:
    -------------------------------------------------------
  */

  const getSignedURL = async (url: any) => {
    try {
      const res = await FileUploadService.getSignedURL(url);
      return res.data.SignedUrl;
    } catch (err) {
      console.log(err);
      throw err; // Rethrow the error to handle it in fetchSignedURLs
    }
  };
  const fetchApiForLoan = async () => {
    console.log(params)
    return await MediaGalleryService.getAll("login", `loanUid=${params.loanUid}`);
  };
  const fetchApiForPD = async () => {
    return await MediaGalleryService.getAll("pd", `loanUid=${params.loanUid}`);
  };

  async function fetchData() {
    try {
      // Make API calls
      const data1:any = await fetchApiForLoan();
      const data2:any = await fetchApiForPD();

      const filteredData1 = data1.map((item: any) => ({
        uid: item.uid,
        name: item.name,
        url: item.url,
      }));
      const filteredData2 = data2.map((item: any) => ({
        uid: item.uid,
        name: item.name,
        url: item.url,
      }));

      const signedData1 :any = await Promise.all(
        filteredData1.map(async (item: any) => ({
          ...item,
          signedURL: await getSignedURL(item.url),
        }))
      );
      setMediaGalleryForLogin(signedData1)
      const signedData2:any = await Promise.all(
        filteredData2.map(async (item: any) => ({
          ...item,
          signedURL: await getSignedURL(item.url),
        }))
      );
      setMediaGalleryForPd(signedData2)
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  }

  function filterData(res: any) {
    // Filter out only the uid and name properties for each object
    const filteredData = res.map((obj: any) => ({
      uid: obj?.uid,
      placeOfUpload: obj?.placeOfUpload,
      name: obj?.name,
      fileName: obj?.fileName,
      category: obj?.category,
      type: obj?.type,
      sectionName: obj?.sectionName,
      fileType: obj?.fileType,
      download: obj?.download,
      url: obj?.url,
    }));

    return filteredData;
  }

  const searchAutoCompleteCreatedBy = async (event: any) => {
    let suggestionsTemplate = [];
    if (!event.query.trim().length) {
      suggestionsTemplate = createdByTemplate;
    } else {
      await UserService.getAll(0, 10, `name=${event.query}&`).then((res) => {
        if (res.status === 200) {
          suggestionsTemplate = [...res.data];
          // setIsLoading(false);
        }
      });
      if (suggestionsTemplate.length === 0) {
        suggestionsTemplate.push({ name: undefined, designation: undefined, emailId: undefined });
      }
    }
    setCreatedByTemplate(suggestionsTemplate);
  };

  /* 
    -------------------------------------------------------
      ButtonClick Functions:
    -------------------------------------------------------
  */
  const onSearch = () => {
    let search = "";
    if (searchObj.documentCategory) {
      search = `${search}name:lk=${searchObj.documentCategory}&`;
    }

    if (searchObj.documentType) {
      search = `${search}name:lk=${searchObj.documentType}&`;
    }

    if (searchObj.documentCategory) {
      search = `${search}name:lk=${searchObj.documentCategory}&`;
    }

    if (searchObj.documentCategory) {
      search = `${search}name:lk=${searchObj.documentCategory}&`;
    }

    if (searchObj.documentCategory) {
      search = `${search}name:lk=${searchObj.documentCategory}&`;
    }

    setDataTable({
      ...dataTable,
      search,
    });
  };
  const onReset = () => {
    setSearchObj({
      documentCategory: "",
      documentType: "",
      section: "",
      stage: "",
      fileType: "",
    });
    setDataTable({
      ...dataTable,
      search: "",
    });
  };

  /* 
    -------------------------------------------------------
      Sectional Render Functions: For Sub components / section wise renders
    -------------------------------------------------------
  */

  const renderFilter = () => {
    const { documentCategory, documentType } = searchObj;
    return (
      <>
        <div className="p-6 flex flex-col gap-6 bg-primary-sapphireBlue-100 rounded-lg">
          <div className="flex ">
            <SectionTitle title="text.masterData.types.search" />
          </div>
          <div className=" grid grid-cols-1 md:grid-cols-4 gap-3">
            <AutoComplete
              label="Document Category"
              name="createdBy"
              field="name"
              value={documentCategory}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={createdByTemplate}
              completeMethod={searchAutoCompleteCreatedBy}
              onChange={handleAutoCompleteFilterChange}
              itemTemplate={itemTemplate}
            />
            <SimpleDropDown
              label="Document Type"
              name="documentType"
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
              options={IS_ACTIVE}
              onChange={handleFilterChange}
              value={documentType}
            />
            <SimpleDropDown
              label="Section"
              name="documentType"
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
              options={IS_ACTIVE}
              onChange={handleFilterChange}
              value={documentType}
            />
            <div></div>
            <SimpleDropDown
              label="Stage"
              name="documentType"
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
              options={IS_ACTIVE}
              onChange={handleFilterChange}
              value={documentType}
            />
            <SimpleDropDown
              label="File Type"
              name="documentType"
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
              options={IS_ACTIVE}
              onChange={handleFilterChange}
              value={documentType}
            />
            <div></div>

            <div className="flex gap-5 justify-end">
              <ButtonBox buttonType={BUTTON_TYPE.RESET_OUTLINE} onClick={onReset} />
              <ButtonBox buttonType={BUTTON_TYPE.SEARCH_OUTLINE} onClick={onSearch} />
            </div>
          </div>
        </div>
      </>
    );
  };

  const itemTemplate = (item: any) => {
    if (item.name === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <img src={LoadingIcon} alt="search-icon" className=" h-7 w-7" />
          <Text className="text-primary-labelColor mt-2" label="Loading..." />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  return (
    <div className=" h-[100vh]">
      <div className="flex flex-col gap-6  overflow-auto p-3 bg-white rounded-lg">
        <div className="flex w-full justify-between items-center">
          <Text
            label="Application Media"
            className="text-primary-ptext-1000 text-h2 font-bold flex"
          />
        </div>
        {renderFilter()}

        <Section className="border-2 ">
          <div className="flex flex-col gap-3">
            <Text label={"LOGIN"} />
            <div className="  overflow-y-auto grid grid-cols-8 gap-4">
              {mediaGalleryForLogin.map((el: any, i: any) => (
                <div key={i} className="flex flex-col items-center" onClick={handleImageClick}>
                  <div>
                    <img src={el.url} alt="image" className="w-20 h-20" />
                  </div>
                  <div>{el?.name}</div>
                </div>
              ))}
            </div>
          </div>
        </Section>
      </div>

      <MediaGallery
        visible={isImageClicked}
        list={mediaGalleryForLogin}
        onToggleVisibility={() => setIsImageClicked(false)}
      />
    </div>
  );
}

export default withRouter(PageApplicationMedia);
